.body {

  font-family: "PT Sans", "Arial", sans-serif;
  margin: 0 auto;

  @media (max-width: @mobile-width-only) {
    width: 320px;
  }

  @media (min-width: @tablet-width) {
    width: 100%;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
    box-shadow: 15px 0 30px @modest-grey, -15px 0 30px @modest-grey;
  }
}
