.copyright {
  display: flex;
  justify-content: start;
  margin-bottom: 15px;
}

.copyright__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 26px;
  text-transform: uppercase;
  margin-top: 4px;
  margin-right: 0;
  margin-left: 96px;

  @media (min-width: @tablet-width) and (max-width: @desktop-width - 1px) {
    margin-left: calc(191% - 128px);
  }
}

.copyright__link {
  margin-top: 0;
  margin-left: 13px;
  fill: @black;
  order: 1;

  &:hover {
    fill: @blue;
  }

  &:active {
    fill: @black;
    opacity: 0.3;
  }
}
